.newsCard {
    width: 70%;
    background-color: rgb(7, 7, 7);
    margin: 50px auto;
    padding: 10px;
    box-shadow: 0 0 50px rgba(0, 0, 0, 0.699);
    max-height: 500px;
    transform: rotateZ(-18deg);
}

.newsCard > div {
    width: 100%;
}
.newsCard > h3,h6 {
    margin: 0;
    letter-spacing: 1px;
}

.newsCard > h3 {
    color: rgba(255, 255, 255, 0.938);
    font-family: 'Crimson Text', serif;
    margin-top: 10px;
}
.newsCard > h6 {
    margin-top: 3px;
    color: rgba(255, 255, 255, 0.74);
}

.newsCard > p {
    margin: 0;
    margin: 10px 0;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.801);
    font-family: 'Oxygen', sans-serif;
}

.read {
    border: 0;
    padding: 1px;
    background-color: rgba(255, 255, 255, 0.699);
    font-size: 12px;
}
.read:hover {
    background-color: lightblue;
}